<template>
  <div class="depthcustomhome depthcustomdetails orderdetails">
    <div class="shopcar clearfix">
      <div class="shopcarleft w60 pull-left pl-25">
        <div class="header">
          <div class="content">
            <img src="../../../assets/image/icon/shopcar-productlist.png" height="29" width="38" alt="">
            <span></span>
          </div>
        </div>
        <div class="shopcarrightcontent pr0">
          <ul class="imglist clearfix" style="margin-bottom: 0">
            <li v-for="item in imagesData"><img :src="item" height="324" width="493" alt=""></li>
          </ul>
          <table class="table table-striped table-hover import-hover mb-4 mt-4">
            <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item,index) in acceptData.orderinfo.info">
              <td class="index">{{index}}</td>
              <td>
                <span v-if="item.texture == k" class="type" v-for="k in typelist">{{k}}</span>
              </td>
              <td>
                <span class="project">{{item.weight}}</span>
              </td>
              <td>
                <span class="index">{{item.num}} <span></span>{{item.parameteritemunit}}</span>
              </td>
              <td>
                <input class="w100 tac" disabled="disabled" type="text" name="" :value="item.unitprice" >
              </td>
              <td>
                <input disabled="true" class="w100  tac" type="text" name="" :value="item.totalprice" >
              </td>
            </tr>
            </tbody>
          </table>
          <div class="w100 bt1 bb1 pt-4 pb-4 other ">
            <div class="clearfix">
              <p class="pull-left">
                <span class="fc-gray"></span
                ><span class="select">{{acceptData.orderinfo.handsize}}</span>
              </p>
              <p class="pull-left"><span class="fc-gray"></span>
                <input disabled="true"  type="text" name="" id="" :value="acceptData.orderinfo.lettering">
              </p>
            </div>
            <div class="textareabox">
              <span style="line-height: .5rem" class="fc-gray"></span>
              <span class="textarea">{{acceptData.cremark}}</span>
            </div>
          </div>
          <div class="bottom">
            <div class="total pb-2">
              <span class="f36 "></span>
              <span class="fc-blue">  <span class="fc-blue fw-b f36">{{acceptData.price}}</span> </span>
            </div>
            <p class="fc-gray pb-2">10%</p>
          </div>
        </div>
      </div>
      <div class="shopcarright w40 pull-left pl1 pr-25">
        <div class="header">
          <div class="content">
            <img src="../../../assets/image/icon/shopcar-productdetails.png" height="27" width="34" alt="">
            <span></span>
          </div>
        </div>
        <div class="shopcarrightcontent">
          <form action="javascript:;" @submit="pushorder()">
            <ul>
              <li class="clearfix">
                <span class="title w20"> </span>
                <el-input @focus="handleSelectname" v-bind:class="{checkoutbox: clientshowname}" class="w80"
                          v-model="client.name" placeholder=""></el-input>
              </li>
              <li class="clearfix">
                <span class="title"> </span>
                <el-input @focus="handleSelectphone" v-bind:class="{checkoutbox: clientshowphone}" class="w80"
                          v-model="client.phone" placeholder=""></el-input>
              </li>
              <li class="clearfix">
                <span class="title"> </span>
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 2, maxRows: 6}"
                  placeholder=""
                  v-model="client.txt">
                </el-input>
              </li>
              <li class="clearfix line pr">
                <span class="title"></span>
                <div class="w80"></div>
              </li>
              <li class="clearfix">
                <span class="title"> </span>
                <el-input step="0.01" type="number" class="w60" placeholder="" v-model="pricebefore">
                  <template slot="append"></template>
                </el-input>
                <tt v-if="(/^[1-9]\d*$/.test(pricebeforetip)) && parseFloat(pricebeforetip) < 100">
                  <tt class="pl48" >{{pricebeforetip}}</tt>
                  <tt>%</tt>
                </tt>
              </li>
              <li class="clearfix">
                <span class="title"> </span>
                <el-input step="0.01" type="number" class="w60" placeholder="" v-model="priceafter">
                  <template slot="append"></template>
                </el-input>
                <tt v-if="(/^[1-9]\d*$/.test(priceaftertip)) && parseFloat(priceaftertip) < 100">
                  <tt class="pl48">{{priceaftertip}}</tt>
                  <tt>%</tt>
                </tt>
              </li>
              <li class="clearfix line pr">
                <span class="title"></span>
                <div class="w80"></div>
              </li>
              <li class="clearfix">
                <span class="title"> </span>
                <el-input @focus="handleSelectman" v-bind:class="{checkoutbox: clientshowman}" v-model="salesman"
                          placeholder=""></el-input>
              </li>
              <li class="clearfix line pr">
                <span class="title"></span>
                <div class="w80"></div>
              </li>
              <li class="clearfix">
                <span class="title"></span>
                <button type="submit" class="mybtn w80 pull-left"> </button>
              </li>
            </ul>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    created(){
      this.getdata()
    },
    data(){
      return {
        /**/
        acceptData: '',
        /**/
        imagesData: '',
        /*client*/
        client: {
          name: '',
          phone: '',
          txt: '',
        },
        /*price*/
        pricebefore: '',
        pricebeforetip: '',
        priceafter: '',
        priceaftertip: '',
        /**/
        salesman: '',
        /**/
        clientshowname: false,
        clientshowphone: false,
        clientshowman: false,
        /**/
        typelist:'',
      }
    },
    methods: {
      /**/
      handleSelectname(){
        if (this.clientshowname) {
          this.clientshowname = false;
        }else if(this.client.name == ""){
          this.clientshowname = false;
        }
      },
      handleSelectphone(){
        if (this.clientshowphone) {
          this.clientshowphone = false;
        }else if(!(/^1(3|4|5|7|8)\d{9}$/.test(this.client.phone))){
          this.clientshowphone = false;
        }
      },
      handleSelectman(){
        if (this.clientshowman) {
          this.clientshowman = false;
        }else if(this.salesman == ""){
          this.clientshowman = false;
        }
      },
      /**/
      pushorder () {
        /**/
        if (this.client.name == "" ) {
          this.messagedata = '';
          this.message();
          this.clientshowname = true;
          return false
        }else {
          this.clientshowname = false;
        }
        /**/
        if (!(/^1(3|4|5|7|8)\d{9}$/.test(this.client.phone))) {
          this.messagedata = '';
          this.message();
          this.clientshowphone = true;
          return false
        }else {
          this.clientshowphone = false;
        }
        /**/
        if (this.salesman == "") {
          this.messagedata = '';
          this.message();
          this.clientshowman = true;
          return false
        }else {
          this.clientshowman = false;
        }
        /*<0*/
        if(this.pricebefore < 0 || this.priceafter < 0){
          this.messagedata = '   0';
          this.message();
          return false
        }
        var b = ''
        if(this.pricebefore == '' || this.pricebefore == 0){
          this.priceafter = this.acceptData.price
        }
        if(this.priceafter == '' || this.priceafter == 0){
          this.pricebefore = this.acceptData.price
        }
        //1.
        var url = this.BASEURL+'/index.php?action=sureorder';
        this.$http.get(url, {
          params: {
            order_id: this.acceptData.order_id,
            cname: this.client.name,
            cphone: this.client.phone,
            remark: this.client.txt,
            price: this.acceptData.price,
            deposit: this.pricebefore,
            tailcash: this.priceafter,
            salename: this.salesman,
          }
        }, {}).then(response => {
            if (response.body.result == 'SUCCESS') {
              this.$confirm('', {
                confirmButtonText: '',
                cancelButtonText: '',
                confirmButtonClass: 'btn-b',
                cancelButtonClass: 'btn-w',
              }).then(() => {
                this.$router.push({name: 'Index'});
              }).catch(() => {
                this.$router.push({name: 'OrderlistHome'});
              });
            } else if (response.body.error_code == '90009' || response.body.error_code == '90005') {
              parent.location.href = '/login';
              return false
            }
          },
          response => {
            console.log(response);
          });
      },
      /**/
      getdata(){
        if (this.$route.query.msg) {
          this.acceptData = JSON.parse(this.$route.query.msg)
        }
        if (this.acceptData.orderinfo) {
          this.acceptData.orderinfo = JSON.parse(this.acceptData.orderinfo)
        }
        if (this.$route.params.images) {
          this.imagesData = this.$route.params.images
        }
        if (this.$route.query.typelist) {
          this.typelist = this.$route.query.typelist
        }
      },
      /*,*//*,*/
      countprice (n) {
        //,totalPrice,this.pricediscountsafter
        var a = this.acceptData.price;
        if (n)/*,*/{
          this.priceafter = parseFloat(a - this.pricebefore)
          this.pricebefore = parseFloat(a - this.priceafter)
          this.pricebeforetip = this.priceratio(a,this.pricebefore)
          this.priceaftertip = this.priceratio(a,this.priceafter)
        } else  /*,*/{
          this.pricebefore = parseFloat(a - this.priceafter)
          this.priceafter = parseFloat(a - this.pricebefore)
          this.pricebeforetip = this.priceratio(a,this.pricebefore)
          this.priceaftertip = this.priceratio(a,this.priceafter)
        }
        if(this.priceafter.toString().length>12){
          this.priceafter = this.priceafter.toFixed(2)
        }
        if(this.pricebefore.toString().length>12){
          this.pricebefore = this.pricebefore.toFixed(2)
        }
      },
    },
    watch: {
      $route(){
        this.getdata()
      },
      /*,*/
      pricebefore: function (val) {
        this.countprice(true);
      },
      priceafter:function() {
        this.countprice(false);
      },
    }
  }
</script>
<style scope>


</style>



